.BuildControl_BuildControl__3qwVA {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 2px 0;
    --width: 50px
}

.BuildControl_BuildControl__3qwVA button {
    display: block;
    font: inherit;
    padding: 5px;
    margin: 0 5px;
    border: 1px solid #AA6817;
    cursor: pointer;
    outline: none;
    width: var(--width);
}

.BuildControl_BuildControl__3qwVA button:disabled {
    background-color: #AC9980;
    border: 1px solid #7E7365;
    color: #ccc;
    cursor: default;
}

.BuildControl_BuildControl__3qwVA button:hover:disabled {
    background-color: #AC9980;
    color: #ccc;
    cursor: not-allowed;
}

.BuildControl_Label__3EfNH {
    padding: 10px;
    font-weight: bold;
    width: var(--width);
}

.BuildControl_BuildControl__3qwVA .BuildControl_Less__3RwUd {  
    background-color: #D39952;
    color: white;
}

.BuildControl_BuildControl__3qwVA .BuildControl_More__2IZnf {
    background-color: #8F5E1E;
    color: white;
}

.BuildControl_BuildControl__3qwVA .BuildControl_Less__3RwUd:hover, .BuildControl_BuildControl__3qwVA .BuildControl_Less__3RwUd:active {  
    background-color: #DAA972;
    color: white;
}

.BuildControl_BuildControl__3qwVA .BuildControl_More__2IZnf:hover,.BuildControl_BuildControl__3qwVA .BuildControl_More__2IZnf:active {
    background-color: #99703F;
    color: white;
}
@media (min-width: 320px) {
    .BuildControl_Label__3EfNH, .BuildControl_BuildControl__3qwVA button {
        width: 80px;
    }
}
.BuildControls_BuildControls__1Xi_J {
