    flex-flow: column;
    justify-content: center;
    align-items: center;
    box-shadow: 0 2px 1px #ccc;
    margin: auto;
    padding: 10px 0;
    /* font-size: 0.9rem; */
}
.BuildControls_BuildControls__1Xi_J p {
