.BuildControls_OrderButton__1U7Bj {
    background-color: #becf2d;
    padding: 10px;
    border-radius: 4px;
    outline: none;
    border: none;
    color: #7c581c;
    font-size: 1.05rem;
    box-shadow: 1px 1px 1px 1px #727c1b;
    font-weight: 400;
}

.BuildControls_OrderButton__1U7Bj:hover, .BuildControls_OrderButton__1U7Bj:active {
    background-color: #d2dd6c;
    /* background-color: rgb(226, 19, 19); */
    /* color: #cf8f2e; */
