}

.BuildControls_OrderButton__1U7Bj:disabled {
    cursor: not-allowed;
    color: gray;
    background-color: darkgray;
    box-shadow: 1px 1px 1px 1px gray;
    -webkit-animation: BuildControls_disable__1ca1i 0.4s linear;
            animation: BuildControls_disable__1ca1i 0.4s linear;
}

.BuildControls_OrderButton__1U7Bj:not(:disabled){
