}

@-webkit-keyframes BuildControls_enable__3KbEx {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    33% {
        -webkit-transform: scale(1.05);
                transform: scale(1.05);
    }
    50% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    66% {
        -webkit-transform: scale(1.05);
                transform: scale(1.05);
    }
